import React, { useEffect, useRef } from "react"
import Style from "./style.module.scss"
import LinkedIn from "./Links/LinkedIn"
import Gmail from "./Links/Gmail"
import Github from "./Links/Github"
import anime from "animejs"

export default function Home() {
    const titleRef = useRef(null)
    const linkRef = useRef(null)
    useEffect(() => {
        anime({
            targets: titleRef.current.querySelectorAll(".animate"),
            translateY: ["200%", 0],
            easing: "easeInOutQuad",
            duration: 800,
            delay: anime.stagger(350, { start: 150 }),
        })
        anime({
            targets: linkRef.current,
            opacity: [0, 1.0],
            easing: "easeInOutQuad",
            duration: 800,
            delay: anime.stagger(350, { start: 1350 }),
        })
    })
    return (
        <div className={`main__container ${Style.hero}`} ref={titleRef}>
            <div className="main__container__title" style={{ width: "100%" }}>
                <div className={Style.inner}>
                    <div>
                        <h3 style={{ marginBottom: "5px" }} className="animate">
                            Hello
                            <span role="img" aria-label="hi">
                                
                            </span>
                        </h3>
                    </div>
                    <div>
                        <h1 className={`${Style.title} animate`}>
                            I'm Rohit Chaudhari
                        </h1>
                    </div>
                    <div>
                        <h2 className="animate">Software Developer</h2>
                    </div>
                </div>
                <div
                    className={`${Style.main__links} ${Style.inner}`}
                    ref={linkRef}
                >
                    <Github className={Style.svg__links} />
                    <Gmail className={Style.svg__links} />
                    <LinkedIn className={Style.svg__links} />
                </div>
            </div>
        </div>
    )
}
