import React from "react"
import Style from "./style.module.scss"

const Contact = ({ duration, anim, easing }) => {
    return (
        <div className="main__container" id="contact">
            <div className={`main__container__title ${Style.container}`}>
                <h2
                    data-sal={anim}
                    data-sal-delay={duration}
                    data-sal-easing={easing}
                >
                    Connect With Me
                </h2>
                <div>
                    <div>
                        <p
                            data-sal={anim}
                            data-sal-delay={duration}
                            data-sal-easing={easing}
                            className={Style.desc}
                        >
                            I hope you enjoyed this website as much as I've
                            enjoyed making it. I am always interested in new
                            stuff, feel free to reach me via{" "}
                            <a href="mailto:rohitkc2810@gmail.com">
                                rohitkc2810@gmail.com
                            </a>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Contact
