import React from "react"
import Style from "./footer.module.scss"
import Github from "../Hero/Links/Github"
import Gmail from "../Hero/Links/Gmail"
import LinkedIn from "../Hero/Links/LinkedIn"

const Footer = () => {
    return (
        <div
            className="main__container"
            style={{
                marginBottom: "0",
                padding: "1rem 1rem",
                paddingTop: "4.5rem",
            }}
        >
            <div className={`main__container__title ${Style.container}`}>
                <div>
                    <span>
                        Made with
                        <span role="img" aria-label="love">
                            
                        </span>{" "}
                        by Rohit Chaudhari
                    </span>
                </div>
                <div>
                    <Github className={Style.svg__links} />
                    <Gmail className={Style.svg__links} />
                    <LinkedIn className={Style.svg__links} />
                </div>
            </div>
        </div>
    )
}

export default Footer
